import { Component, OnInit, OnDestroy } from '@angular/core';
import { FirebaseService } from '../firebase.service';


@Component({
  selector: 'home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit, OnDestroy {

  private subscription;
  private subscription2;
  public profilePicUrl;
  public introText: string = ""

  constructor(private firebaseService : FirebaseService) { }

  ngOnInit() {
    let url = localStorage.getItem('profile_pic_url');
    if(!url){
    this.subscription = this.firebaseService.fetchProfilePic()
      .subscribe((url) => {
        this.profilePicUrl = url;
        localStorage.setItem('profile_pic_url',url);
      });
    }
    else{
      this.profilePicUrl = url;
    }

    this.subscription2 = this.firebaseService.fetchIntroText().subscribe(
      (text) => {
        this.introText = text
        this.subscription2.unsubscribe();
      }
    )

  }

  ngOnDestroy(){
    if(this.subscription)
      this.subscription.unsubscribe();

    if(this.subscription2)
      this.subscription2.unsubscribe();
  }

}



